import React from "react";
import {
	Button,
	Input,
} from "@ya.praktikum/react-developer-burger-ui-components";

import AdditionalAction from "../../components/ui/AdditionalAction/AdditionalAction";
import AuthTemplate from "../../components/AuthTemplate/AuthTemplate";
import { useFormik } from "formik";
import { ResetPasswordSchema } from "../../utils/validateSchemas";
import { postPasswordResetFetch } from "../../services/requests";
import { Navigate, useLocation } from "react-router-dom";
import PasswordInput from "../../components/ui/PasswordInput/PasswordInput";
import { ILocation } from "../../utils/types";
import { useAppDispatch, useAppSelector } from "../../services";

function ResetPasswordPage() {
	const dispatch = useAppDispatch();
	const reseted = useAppSelector(
		(store) => store.authSlice.resetPasswordStatus === "reseted"
	);

	const {
		handleChange,
		handleSubmit,
		handleBlur,
		values,
		isValid,
		dirty,
		errors,
		touched,
	} = useFormik({
		initialValues: {
			password: "",
			token: "",
		},
		onSubmit(values) {
			dispatch(postPasswordResetFetch(values) as any);
		},
		validationSchema: ResetPasswordSchema,
	});

	const inputs = [
		<PasswordInput
			key="password"
			error={touched.password && !!errors.password}
			errorText={errors.password}
			onChange={handleChange}
			onBlur={handleBlur}
			value={values.password}
			placeholder="  "
		/>,
		<Input
			key="token"
			type="text"
			name="token"
			error={touched.token && !!errors.token}
			errorText={errors.token}
			onChange={handleChange}
			onBlur={handleBlur}
			value={values.token}
			placeholder="   "
		/>,
	];

	const additionalActions = [
		<AdditionalAction
			description=" ?"
			linkText=""
			to="/login"
			key="entrance"
		/>,
	];
	const location = useLocation();
	const locationState = location.state as ILocation | undefined
	if (locationState?.from !== "/forgot-password") {
		return <Navigate to="/" replace />;
	}

	return reseted ? (
		<Navigate to="/login" replace />
	) : (
		<AuthTemplate
			title=" "
			inputs={inputs}
			additionActions={additionalActions}
			button={<Button disabled={!isValid || !dirty}></Button>}
			onSubmit={handleSubmit}
		/>
	);
}

export default ResetPasswordPage;
